@import "../../asserts/style/default.scss";

.sg-todo-item-wrap {
  @include element-hover;
  @include flex-center;
  justify-content: space-between;

  background-color: #373737;
  padding: 1rem;
  margin: 0.25rem 0rem;
  border-radius: 5px;

  width: calc(100% - 2rem);

  &.finished {
    .sg-todo-item-icon-check {
      content: url("../../asserts/imgs/MainBody/todo-done.png");
    }
    .sg-todo-item-text {
      text-decoration: line-through;
      color: #a1a1a1;
    }
  }

  .sg-todo-item-icon-check {
    @include icon(1.5rem);

    flex: 1 0 1.5rem;
    max-width: 1.5rem;

    content: url("../../asserts/imgs/MainBody/todo-unfinished.png");

    &:hover {
      content: url("../../asserts/imgs/MainBody/todo-done.png");
    }
  }

  .sg-todo-item-text {
    width: calc(100% - 6rem);
    max-width: calc(100% - 6rem);
    word-wrap: break-word;
    line-height: 1.25;

    input {
      @include input-field;
      width: calc(100%);
      font-size: 1.25rem;
      line-height: 1.25;
      border: 0;
    }
  }

  .sg-todo-item-icon-favorite {
    @include icon(1.5rem);
    flex: 1 0 1.5rem;
    max-width: 1.5rem;
    margin: 0;

    content: url("../../asserts/imgs/MainBody/unfavorite.png");

    &:hover {
      content: url("../../asserts/imgs/SideBar/important.png");
    }

    &.important-view {
      content: url("../../asserts/imgs/MainBody/favorite.png");
    }
  }
}
